@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #36205D;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
}

.center-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.App {
  background-color: #FFFFFF;
  padding: 40px;
}

.error {
  color: #DB2955;
  font-weight: bold;
}

/* User API Form */
.user-api-form .user-id, .user-api-form .api-key {
  width: 300px;
}

.user-api-form .submit-wrapper {
  margin-top: 16px;
}

.user-api-form .label-container {
  display: flex;
  flex-direction: row;
}

.user-api-form .label {
  width: 80px;
}

.app-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.app-controls .date-header {
  display: flex;
  flex-direction: column;
  margin-left: 12px;
  font-size: 1.3em;
  color: #757575;
}

/* Summary Section */
section {
  margin-top: 32px;
}

.section-header, .section-header-title {
  display: flex;
  align-items: center;
}

.show-no-history  {
  margin-left: 8px;
}

section h2 {
  margin-top: 8px;
  margin-bottom: 8px;
}

td {
  height: 100%;
  padding: 0;
}

th {
  text-align: left;
}

.dailys table, .habits table, .todos table {
  width: 100%;
}

.date {
  color: #757575;
}

.date-heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
  font-weight: normal;
  font-size: medium;
}

.link {
  color: #3366BB;
}

.link:hover, .clickable:hover {
  cursor: pointer;
}

/* Task */
.task-name-row {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.task-name {
  padding: 8px;
  max-width: 400px;
}

.task-name p {
  margin: 0;
}

.habit-cell, .daily-cell {
  width: 40px;
}

.habit-score-container {
  display: flex;
  flex-direction: column;
  color: white;
  justify-content: space-evenly;
  height: 40px;
}

/* Success/Fail Symbols */
.success {
  background-color: #24CC8F;
}

.fail {
  background-color: #DB2955;
}

.pass, .habit-cell {
  background-color: #FFDFAE;
}

.pass .symbol {
  color: #FFBE5D;
}

.symbol {
  color: white;
}
.task-icon-container {
  width: 36px;
  height: 22px;
  padding: 4px;
}

.worst {
  background-color: #de3f3f;
}

.worse {
  background-color: #ff6165;
}

.bad {
  background-color: #ff944c;
}

.neutral {
  background-color: #ffbe5d;
}

.good {
  background-color: #24cc8f;
}

.better {
  background-color: #3bcad7;
}

.best {
  background-color: #50b5e9;
}
.todos ul {
  padding: 0;
}

.todo-row {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 1px;
}

.todo-color {
  width: 30px;
  margin-top: 1px;
  margin-bottom: 1px;
}

.todo-notes {
  color: #70757a;
  font-size: small;
}

.todo-date {
  padding: 5px;
}
